<template>
  <div>
    <editor
      v-model="text"
      height="400"
      apiKey="pgm9gcnllfimfcgo7joyxpm22j4ov87erg4u4fyy7gxo1g23"
      :init="tinymceInit"
    ></editor>
    <el-dialog
      :visible.sync="dialogXplVisible"
      width="85%"
      top="10vh"
      class="dialog-image zindex68k"
      id="xpl"
    >
      <xpl
        :mode="{
          multiple: false
        }"
        @eventDone="editorXplSelectedHandler"
        @eventClose="dialogXplVisible = false"
      ></xpl>
    </el-dialog>
  </div>
</template>

<script>
  import {Dialog} from 'element-ui'
  import Editor from '@tinymce/tinymce-vue'
  import xpl from 'src/components/UIComponents/xpl/App'
  export default {
    props: ['value', 'heigth'],
    name: "Editor",
    components: {
      ElDialog: Dialog,
      editor: Editor,
      xpl
    },
    data() {
      return {
        dialogXplVisible: false,
        fileCbQueue: [],
        tinymceInit: {
          height: this.heigth || 400,
          theme: 'modern',
          relative_urls: false,
          remove_script_host: false,
          convert_urls: true,
          menubar: false,
          file_picker_callback: this.editorXplCallback,
          file_picker_types: 'file image media',
          plugins: [
            'code advlist autolink lists link image charmap print preview hr anchor pagebreak',
            'searchreplace wordcount visualblocks visualchars code fullscreen',
            'insertdatetime media nonbreaking save table contextmenu directionality',
            'template paste textcolor colorpicker textpattern imagetools table youtube ',
          ],
          toolbar1: 'formatselect | bold italic underline forecolor backcolor | alignleft aligncenter alignright | code',
          toolbar2: 'fontselect fontsizeselect table image  youtube | numlist bullist outdent indent fullscreen',
          table_default_styles: {
            width: '50%'
          },
          image_advtab: true,
          automatic_uploads: true,
          images_upload_base_path: '/uploads',
          imageupload_url: '/',
          valid_elements : '*[*]',
          file_browser_callback_types: 'file image',
        }
      }
    },
    computed: {
      text: {
        get() {
          if (this.value) return this.value;
          return '';
        },
        set(value){
          this.$emit('input', value);
        }
      },
      generateUrl() {
        return location.protocol + '//' + location.host;
      }
    },
    methods: {
      handleInput(field, val) {
        let oldValue = this.value[field];
        this.$emit('input', this.value);
        this.$emit('field-update', field, val, oldValue);
      },
      editorXplCallback(callback, value, meta) {
        this.dialogXplVisible = true;
        this.fileCbQueue.push(callback);
      },
      editorXplSelectedHandler(files) {
        let file = files[0];
        let filename = window.BASE_URL + '/uploads/' + file.name;
        this.fileCbQueue.forEach(fn => fn(filename));
        this.dialogXplVisible = false;
        this.fileCbQueue = [];
      }
    }
  }
</script>

