<template lang="html">
  <el-select
    ref="select"
    class="full-width"
    v-model="selectValues"
    filterable
    clearable
    remote
    :multiple="isMultiple"
    :placeholder="'Tm kim ' + type + '...'"
    :remoteMethod="remoteMethod"
    no-data-text="Khng tm thy kt qu"
    loading-text="ang ti..."
    value-key="id"
    @input="val => handleInput(val)"
  >
    <el-option
      v-for="(item, index) in dataSource"
      :key="index"
      :label="item.label"
      :value="item"
      >
    </el-option>
  </el-select>
</template>

<script>
  import {Select, Option, Message} from 'element-ui'
  import api from 'src/store/api';

  export default {
    props: ['attribute', 'value'],
    components: {
      ElSelect: Select,
      ElOption: Option,
    },
    data() {
      return {
        selectValues: [],
        dataSource:[],
        loading: false,
        isMultiple: true,
        model: this.$util.findModel(this.attribute.type),
        type: this.$util.findModelName(this.attribute.type)
      }
    },
    mounted() {
      this.isMultiple = this.attribute.multiple || this.$util.isMultiple(this.attribute.type);
    },
    watch: {
      value(newVal, oldVal){
        if (newVal){
          if (this.isMultiple) {
            this.selectValues = newVal;
            this.dataSource = newVal;
          } else{
            this.selectValues = newVal.label;
            this.dataSource = Array(newVal);
          }
        }
      }
    },
    methods: {
      remoteMethod(query) {
        if (query !== '') {
          this.loading = true;
          api.get(`/admin/api/object/${this.model}?term=${query}`).then(resp => {
            this.dataSource = resp.data.map(obj => ({
              id: obj.id,
              label: obj.value,
              handle: obj.handle
            }));
            this.loading = false;
          }).catch(e => {
            console.log('e', e);
            Message({
              message: 'C li xy ra, vui lng th li',
              type: 'error'
            });
          })
        } else {
          this.dataSource = [];
        }
      },
      handleInput(val) {
        this.$emit('input', val);
      }
    }
  }
</script>

