<template lang="html">
  <div class="row">
    <div class="col-lg-8 col-md-8 col-sm-12">
      <form-card
        v-for="group of centerGroups"
        :group="group"
        v-model="form"
      ></form-card>
      <custom-field
        v-if="customField.length > 0"
        :group="customField"
        :value="custom_field"
        v-model="custom_field"
      ></custom-field>
    </div>
    <div class="col-lg-4 col-md-4 col-sm-12">
      <div class="card">
        <div class="card-header">
          <h6 class="title">Gallery</h6>
        </div>
        <div class="card-body form-card">
          <div class="col-sm-12">
            <my-select
              class="full-width"
              placeholder="Gallery"
              :attribute="galleries"
              v-model="form.gallery_id"
            >
            </my-select>
          </div>
        </div>
      </div>
      <form-card
        v-for="group of sideGroups"
        hasTop="true"
        :group="group"
        v-model="form"
      >
      </form-card>
    </div>
    <div class="container-fluid">
      <div class="page-action">
        <el-button @click="cancel">Hy</el-button>
        <el-button type="primary" @click="save">Cp nht</el-button>
      </div>
    </div>
  </div>
</template>

<script>
  import FormCard from 'src/components/UIComponents/FormCard.vue'
  import CustomField from 'src/components/UIComponents/CustomField.vue'
  import { Message, Input, Button } from 'element-ui';
  import MySelect from 'src/components/UIComponents/Select'
  import dataFrom from './photo-form'
  import { mapState } from 'vuex'

  let _form = {}, _custom_field = {}, firstGroups, secondGroups;

  export default {
    components: {
      FormCard,
      ElInput: Input,
      ElButton: Button,
      Message,
      MySelect,
      CustomField
    },
    beforeCreate() {
      firstGroups = dataFrom[0].groups;
      secondGroups = dataFrom[1].groups;

      firstGroups.forEach(group => {
        group.attributes.forEach(attr => {
          if (attr.multiple && _form[attr.prop]) {
            _form[attr.prop] = [];
          } else {
            _form[attr.prop] = '';
          }
          attr.value = _form[attr.prop];
        })
      })
      secondGroups.forEach(group => {
        group.attributes.forEach(attr => {
          if (attr.multiple && _form[attr.prop]) {
            _form[attr.prop] = [];
          } else {
            _form[attr.prop] = '';
          }
          attr.value = _form[attr.prop];
        })
      })

      _form['gallery_id'] = this.$route.params.galleryID - 0;
      let id = this.$route.params.id;
      this.$store.dispatch('getCustomField', {
        type: 'photo',
        id: id
      });
      this.$store.dispatch('fetchGalleries');
    },
    data () {
      return {
        centerGroups: firstGroups,
        sideGroups: secondGroups,
        custom_field: _custom_field,
        show_fields: []
      }
    },
    computed: {
      form: {
        get () {
          return this.$store.state.photoDetail;
        },
        set (value) {}
      },
      customField: {
        get() {
          let data = this.$util.filterByHandle(this.$store.state.customField, this.show_fields);
          for (let item of data) {
            if (item.value) {
              _custom_field[item.handle] = item.value;
            } else {
              _custom_field[item.handle] = '';
            }
          }
          return data||'';
        },
        set(value) {}
      },
      galleries() {
        let data = this.$store.state.galleries;
        let arr = {};
        arr.options = [];
        for (let item of data) {
          arr.options.push({
            value: item.id,
            title: item.title
          });
        }
        return arr;
      },
    },
    mounted() {
      this.getContext();
      let id = this.$route.params.id;
      this.$store.dispatch('fetchPhotoDetail', id);

      this.$store.dispatch('setPageTitle', 'Cp nht photo');
      this.$store.dispatch('setCurrentActions', [{
        label: 'Cp nht',
        type: 'primary',
        icon: '',
        callback: this.save
      }])
    },

    methods: {
      cancel() {
        let galleryID = this.$route.params.galleryID;
        this.$router.push('/galleries/' + galleryID);
      },
      save() {
        let self = this;
        this.$validator.validateAll().then(function (result) {
          if (result) {
            self.$store.dispatch('updatePhoto', self.form).then(res => {
              self.$store.dispatch('storeCustomField', self.$util.parseDataCustomfield(self.customField, self.custom_field, 'photo', self.form.id));
              Message({
                message: 'Cp nht thnh cng',
                type: 'success'
              });
              self.$router.push('/galleries/' + self.form.gallery_id);
            }, res => {
              Message({
                message: res.message,
                type: 'error'
              });
            });
          }
        })
      },
      getContext() {
        let ctx = this.$util.getContext(this.form, {module: 'photo'});
        this.$store.state.ctx = ctx;
        return ctx;
      },
    },
    watch: {
      'form': {
        handler(newVal, oldVal) {
          let ctx = this.getContext();
          let output = window.__FUNC.CustomField(ctx);
          this.show_fields = output;
        },
        deep: true
      }
    },
    destroyed(){
      this.$store.dispatch('setCurrentActions', []);
      this.$store.state.listThemeView = [];
      this.$store.state.customField = [];
    }
  }
</script>
